.tourlist {
    width: 70vw;
    margin: 0 auto;
    padding: 2rem 0;
}

@media screen and (min-width:768px){
    .tourlist {
        display: grid;
        grid-template-columns: 1fr 1fr;//1fr distributed evenly
        grid-column-gap: 1.5rem;
    }
}
@media screen and (min-width:992px){
    .tourlist {
        width: 85vw;
        grid-template-columns: repeat(3, 1fr);//1fr distributed evenly
    
    }
}
@media screen and (min-width:1140px){
    .tourlist {
        grid-template-columns: repeat(4, 1fr);//1fr distributed evenly
    }
}

